import React from 'react';
import './Grid450.scss';
import PropTypes from 'prop-types';

function Grid450(props) {
    const { children, isPrimary } = props;

    const prm = isPrimary ? "primarygrid" : 'secondarygrid';

    return (
        <div className={[`grid450wrapper`, prm].join(' ')}>
            {children}
        </div>
    );
}

Grid450.prototype = {
    children: PropTypes.node,
    userName: PropTypes.string,
    isHead: PropTypes.bool,
    isPrimary: PropTypes.bool,
}

Grid450.defaultProps = {
    userName: 'Dimantha',
    isHeader: true,
    isPrimary: false,

}

export default Grid450;